#main {
  display: grid;
  grid-template-columns: auto 350px;
  background-color: #0f1e2c;
  height: 100vh;
}
#preview {
  width: 100%;
  height: 100%;
  border: none;
}
nav {
  max-height: 100vh;
  overflow: scroll;
  display: grid;
  grid-template-rows: 75px auto 75px;
  color: #fff;
}
#badges, #crawler {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
#badges h4 {
  line-height: 40px;
  width: 40px;
  text-align: center;
  background-color: red;
  border-radius: 50%;
  margin-right: 15px;
  transition:
    color .2s ease,
    background-color .2s ease;
}
#badges h4:hover {
  background-color: rgb(0, 174, 91);
  cursor: pointer;
}
#categories {

}
.category {
  overflow: scroll;
}
table {
  width: 100%;
}
caption {
  line-height: 40px;
}
td {
  line-height: 30px;
  color: rgba(255,255,255,.5);
  border-bottom: solid 1px rgba(255,255,255,.1);
  cursor: pointer;
  padding-left: 5px;
}
td:hover, .checked {
  color: #fff;
  background: rgba(0,0,0,.2);
}
.checked {
  box-shadow: rgb(0, 174, 91) 3px 0px 0px 0px inset;
}
td input {
  margin: 0 10px;
}
.shade {
  opacity: 0.25;
}
button {
  line-height: 40px;
  width: 123px;
  border: solid 1px #00ae5b;
  background-color: transparent;
  color: #00ae5b;
  font-size: 1em;
  outline: none !important;
  transition:
    background-color .2s ease,
    color .2s ease;
}
button:hover {
  background-color: #00ae5b;
  color: #fff;
  cursor: pointer;
}
#search {
  display: none;
}
#search.open {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,0,0,0.75);
}
